
.header{
  background-color: #f0b411;
}

img{
  width:2rem;
  height:2rem;
}
h1 {
  text-align: center;
  background-color: #f4a226;
  font-family: "Monoton";
  font-size: 2rem;
  text-transform: uppercase;
  word-spacing: 10px;
  padding: 2.4rem 0;
  margin-top: 0px;
  text-shadow: 3px 3px 5px rgb(0,0,0,0.2)
}

body{
  margin:0px;
  font-family: sans-serif;
  color: #5a3e2b;
  font-family: "Quicksand";
  font-weight: 500;
}


.segment{
  width: 25px;
  height: 6.25px;
  background-color: #3498db; 
  position: absolute;
  clip-path: polygon(16.6% 0%, 83.3% 0%, 100% 50%, 83% 100%, 16% 100%, 0% 50%);
  background-color: transparent;

}

.segmentRed{
  background-color:red;
}

.sevenSegmentPosition{
  left:1300px;
  position:absolute; 

}

.main{
  margin: 0 auto;
  background-color: bisque;
  width:auto;
  height:100vh;
  align-items: center;
  justify-content: center;
 text-align: center;
}

.grid-container {
  padding:4rem;
  display: grid;
  text-align: center;
  grid-template-columns: auto auto; 
  grid-gap: 10px; 
}

.grid-container3R3C {
  position:"relative";
  gap:0px;
  display: grid;
  grid-template-columns: 15vh 15vh 15vh ;
  grid-template-rows: 15vh 15vh 15vh;
justify-content: center;

}

.grid-container3R3C>div {
  display:flex;
  border: 1px solid black;
  background-color: white;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  
}

.React-title{
  font-size: 10rem;
}

.button{
  background-color: #f4a226;
  border-radius: 10rem;
  padding: 1.2rem 3.2rem;
  font-weight: 700;
  cursor: pointer;
  box-shadow:0 0 30px grey;
  text-shadow: 3px 3px 5px rgb(0,0,0,0.2)
}